import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import TagList from './TagList';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Icon from '@material-ui/core/Icon';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  card: {
    width: 385,
    height: 40,
  },
  expansionpanel: {
    width: 432,
    height: 58,
  },
  heading: {
    fontSize: theme.typography.pxToRem(28),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
}));

const ClockList = ({ projects, color, cnumber }) => {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <div className={classes.root}>
      {TagList({projects}) && TagList({projects}).map((section, idx) => {
        return (
          <ExpansionPanel
            key={idx}
            expanded={expanded === `panel${section.tag}`}
            onChange={handleChange(`panel${section.tag}`)}
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/${color}.png)`,
              color: 'white'
            }}
          >
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon style={{ color: 'white' }} />}
              aria-controls={`panel${section.tag}bh-header`}
              id={`panel${section.tag}bh-header`}
            >
              <Typography component="h3" className={classes.heading}>
                <Box fontWeight="fontWeightBold">
                  {section.tag}
                </Box>
              </Typography>
            </ExpansionPanelSummary>
            {section.projects && section.projects.map((project, idx) => {
              return (
                <div key={idx}>
                    <ExpansionPanelDetails
                      className={classes.expansionpanel}
                      style={{
                        backgroundColor: `${cnumber}`
                      }}
                    >
                      <Link to={'/c/' + project.tag.toLowerCase() + '/' + project.id} key={idx}>
                        <Card
                          className={classes.card}
                          style={{
                            backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/${color}.png)`,
                            color: 'white'
                          }}
                        >
                          <CardContent style={{ padding: '4px' }}>
                            <Grid container spacing={1}>
                              <Grid item xs={11}>
                                <Typography component="h5" variant="h5">
                                  {project.title}
                                </Typography>
                              </Grid>
                              <Grid item xs={1}>
                                <CardContent style={{ padding: '3px' }}>
                                </CardContent>
                                <Icon style={{ fontSize: 22 }}>
                                  {project.level}
                                </Icon>
                              </Grid>
                            </Grid>
                          </CardContent>
                        </Card>
                      </Link>
                    </ExpansionPanelDetails>
                </div>
              )
            })}
          </ExpansionPanel>
        )
      })}
    </div>
  )
}

const mapStateToProps = (state) => {
  console.log(state);
  const color = state.color.color;
  return {
    color: color,
  }
}

export default connect(mapStateToProps)(ClockList)