import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  card: {
    maxWidth: 700,
  },
  formControl: {
    margin: theme.spacing(3),
  },
});

class ClockShuffle extends PureComponent {
  state = {
    checkboxes: this.props.idarray.reduce(
      (options, option) => ({
        ...options,
        [option]: false,
      }),
      {}
    ),
  }
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  selectAllCheckboxes = isSelected => {
    Object.keys(this.state.checkboxes).forEach(checkbox => {
      this.setState(prevState => ({
        checkboxes: {
          ...prevState.checkboxes,
          [checkbox]: isSelected
        }
      }));
    });
  };
  selectAll = () => this.selectAllCheckboxes(true);
  deselectAll = () => this.selectAllCheckboxes(false);
  handleCheckboxChange = name => changeEvent => {
    this.setState(prevState => ({
      checkboxes: {
        ...prevState.checkboxes,
        [name]: !prevState.checkboxes[name]
      }
    }));
  };
  handleFormSubmit = formSubmitEvent => {
    formSubmitEvent.preventDefault();
    this.props.history.push({
      pathname: "/c/shuffle/33/selected",
      state: { checkboxes: this.state.checkboxes }
    });
  };
  createCheckbox = option => (
    <Grid item xs={6} key={option}>
      <FormControlLabel
        style={{color: '#01579b'}}
        control={
          <Checkbox 
            checked={this.state.checkboxes[option]}
            onChange={this.handleCheckboxChange(option)}
            value={option}
          />
        }
        label={this.props.projects[option].title}
      />
    </Grid>
  );
  createCheckboxes = () => this.props.idarray.map(this.createCheckbox);

  render() {
    console.log(this.state);
    const { color, classes } = this.props;
    return (
    <div className={classes.root}>
    <Container fixed>
      <Grid container spacing={3}>
        <Grid item xs={12}>
        </Grid>
        <Grid item xs={10}>
          <Card
            className={classes.card}
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/${color}.png)`,
              color: 'white'
            }}
          >
            <CardContent>
              <Typography component="h4" variant="h4">
                Select the favorite clocks
              </Typography>
            </CardContent>
            <CardContent
              style={{
                backgroundColor: '#4fc3f7',
              }}
            >
            <FormControl component="fieldset" className={classes.formControl}>
              <FormGroup row>
                <Grid container>
                  {this.createCheckboxes()}
                </Grid>
              </FormGroup>
            </FormControl>
            </CardContent>
            <CardActions  style={{backgroundColor: '#b3e5fc', color: '#039be5'}}>
              <Button
                size="small"
                variant="contained"
                color="secondary"
                onClick={this.selectAll}
              >
              <Typography variant="subtitle2" color='primary'>
                Select All
              </Typography>
              </Button>
              <Button
                size="small"
                variant="contained"
                color="primary"
                onClick={this.deselectAll}
              >
              <Typography variant="subtitle2" color='secondary'>
                Select None
              </Typography>
              </Button>
              <Button
                size="small"
                variant="contained"
                color="primary"
                onClick={this.handleFormSubmit}
              >
              <Typography variant="subtitle2" color='secondary'>
                Save
              </Typography>
              </Button>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
      </Container>
      </div>
      )
  }
}

const mapStateToProps = (state) => {
  console.log(state);
  const projects = state.project.projects;
  const idarray = Object.keys(projects)
    .filter(project => projects[project].level !== '');
  const color = state.color.color;
  return {
    idarray: idarray,
    projects: projects,
    color: color,
  }
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps))(ClockShuffle)
