import {CHANGE_COLOR} from "../actions/settingActions"


const initialState = {
  color: 'bleu',
  pigment1: '#4fc3f7',
  pigment2: '#81d4fa',
}

export default function color(state = initialState, action) {
  switch ( action.type ) {
    case CHANGE_COLOR:
      if(action.payload === 'bleu'){
        return {
          color: action.payload,
          pigment1: '#4fc3f7',
          pigment2: '#81d4fa',
        }  
      }
      if(action.payload === 'pink'){
        return {
          color: action.payload,
          pigment1: '#f48fb1',
          pigment2: '#f8bbd0',
        }  
      }
      if(action.payload === 'orange'){
        return {
          color: action.payload,
          pigment1: '#ff8a65',
          pigment2: '#ffab91',
        }  
      }
      if(action.payload === 'green'){
        return {
          color: action.payload,
          pigment1: '#aed581',
          pigment2: '#c5e1a5',
        }  
      }
      if(action.payload === 'cyan'){
        return {
          color: action.payload,
          pigment1: '#4dd0e1',
          pigment2: '#80deea',
        }  
      }
      if(action.payload === 'purple'){
        return {
          color: action.payload,
          pigment1: '#b39ddb',
          pigment2: '#d1c4e9',
        }  
      }
      /* falls through */
    default:
      return state;
  }
}