import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { compose } from 'redux'
import { projectSortByAscend } from './TagList';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Icon from '@material-ui/core/Icon';
import ClockBreadcrumbs from './ClockBreadcrumbs';


const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  card: {
    maxWidth: 500,
  },
});

class ClockSummary extends PureComponent {
  render() {
    const { projects, tag, color, cnumber, classes } = this.props;
    return (
      <div className={classes.root}>
        <Container fixed>
          <Grid container spacing={5}>
            <Grid item xs={12}>
            </Grid>
            <Grid>
              <ClockBreadcrumbs breadcrumbsName='summary' tag={tag} />
            </Grid>
            <Grid item xs={12}>
            </Grid>
          </Grid>
          {projectSortByAscend({projects}) && projectSortByAscend({projects}).map((project, idx) => {
            return (
              <div key={idx}>
                {project.tag === tag &&
                  <div>
                    <Grid container spacing={3}>
                      <Grid item xs={6}>
                        <Link to={'/c/' + project.tag + '/' + project.id} key={project.id}>
                          <Card
                            className={classes.card}
                            style={{
                              backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/${color}.png)`,
                              // backgroundColor: '#01579b',
                              color: 'white'
                            }}
                          >
                            <CardContent
                              style={{
                                padding: '5px'
                              }}
                            >
                              <Grid container spacing={3}>
                                <Grid item xs={10}>
                                  <Typography component="h5" variant="h5">
                                    {project.title}
                                  </Typography>
                                </Grid>
                                <Grid item xs={2}>
                                  <CardContent
                                    style={{
                                      padding: '3px'
                                    }}
                                  >
                                  </CardContent>
                                  <Icon style={{ fontSize: 22 }}>
                                    {project.level}
                                  </Icon>
                                </Grid>
                              </Grid>
                            </CardContent>
                            <CardContent
                              style={{
                                // backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/light${color}2.png)`,
                                backgroundColor: `${cnumber}`,
                                padding: '8px'
                              }}
                            >
                              <Typography variant="subtitle1" color="textSecondary">
                                {project.content}
                              </Typography>
                            </CardContent>
                          </Card>
                        </Link>
                      </Grid>
                    </Grid>
                  </div>
                }
              </div>
            )
          })}
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <Link to={'/c/'} key='c'>
                <Card
                  className={classes.card}
                  style={{
                    backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/${color}.png)`,
                    color: 'white'
                  }}
                >
                  <CardContent>
                    <Typography component="h5" variant="h5">
                      Menu
                  </Typography>
                  </CardContent>
                </Card>
              </Link>
            </Grid>
          </Grid>
        </Container>
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  console.log(ownProps);
  const tag = ownProps.match.params.tag
  const projects = state.project.projects;
  const color = state.color.color;
  const cnumber = state.color.pigment2;
  return {
    projects: projects,
    tag: tag,
    color: color,
    cnumber: cnumber,
  }
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps))(ClockSummary)