export const ex = props => {
  const taglist = props.reduce((obj, project) => {
    const tag = project.tag;
    return {
      ...obj,
      [tag]: tag,
    }
  }, {})
  const sections = Object.keys(taglist).map(tag => ({
    tag: tag,
  }))
  return sections
}


export const projectSortByAscend = props => {
  const copyprojects = props.projects.slice();
  const sorted = copyprojects.sort((a, b) => {
    if (a.title > b.title) return 1;
    if (a.title < b.title) return -1;
    return 0;
  });
  return sorted
}

const TagList = props => {
  const projectByTag = projectSortByAscend(props).reduce((obj, project) => {
    const tag = project.tag;
    return {
      ...obj,
      [tag]: [...(obj[tag] || []), project],
    }
  }, {})
  const sections = Object.keys(projectByTag).sort().map(tag => ({
    tag: tag,
    projects: projectByTag[tag],
  }))

  return sections
}

export default TagList