import {SYNC_DATE, parseTime} from "../actions/clockActions"


const initialState = parseTime(new Date());

export default function clock(state = initialState, action) {
  switch ( action.type ) {
    case SYNC_DATE:
      const {hour, minute, second} = action;
      return Object.assign({}, state, {
        hour, minute, second
      });
    default:
      return state;
  }
}