import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { Redirect } from 'react-router-dom'
import { compose } from 'redux'
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ClockBreadcrumbs from './ClockBreadcrumbs';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  card: {
    maxWidth: 700,
  },
  clockcard: {
    width: 655,
    height: 20,
  },
  dialog: {
    width: 800,
    height: 600,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: 'white',
  },
});

class ClockDetails extends PureComponent {
  state = {
  }
  render() {
    console.log(this.props);
    const { id, project, color, classes } = this.props;
    if (project === undefined) return <Redirect to='/c' />
    if (project) {
      return (
        <div className={classes.root}>
          <Container fixed>
            <Grid container spacing={5}>
              <Grid item xs={12}>
              </Grid>
              <Grid>
                <ClockBreadcrumbs breadcrumbsName='start' project={project} />
              </Grid>
              <Grid item xs={12}>
              </Grid>
              <Grid item xs={8}>
                <Card
                  className={classes.card}
                  style={{
                    backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/${color}.png)`,
                    color: 'white'
                  }}
                >
                  <CardContent>
                    <Typography component="h4" variant="h4">
                      {project.title}
                    </Typography>
                  </CardContent>
                  <CardContent>
                    <Typography variant="subtitle1" color="textSecondary">
                      {project.content}
                    </Typography>
                  </CardContent>
                  <CardContent>
                    <Typography variant="subtitle1" color="textSecondary">
                      <div>{project.notes}</div>
                      <div>Posted by Dome</div>
                      <div>3rd September</div>
                    </Typography>
                  </CardContent>
                  <CardActions
                    style={{
                      backgroundColor: '#e1f5fe',
                      color: '#039be5'
                    }}
                  >
                    <Button size="small" variant="contained" color="secondary">
                      <Link to={'/c/' + project.tag + '/' + id + '/start'} key='tag'>
                        <Typography variant="subtitle2" color='primary'>
                          Clock Start
                      </Typography>
                      </Link>
                    </Button>
                    <Button size="small" variant="contained" color="primary">
                      <Link to={'/c/' + project.tag} key='tag'>
                        <Typography variant="subtitle2" color='secondary'>
                          {project.tag}
                        </Typography>
                      </Link>
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            </Grid>
          </Container>
        </div>
      )
    } else {
      return (
        <div className={classes.root}>
          <Container maxWidth="sm">
            <Grid container spacing={5}>
              <Grid item xs={12}>
                <p>Loading...</p>
              </Grid>
            </Grid>
          </Container>
        </div>
      )
    }
  }
}

const mapStateToProps = (state, ownProps) => {
  console.log(ownProps);
  const id = ownProps.match.params.id;
  const projects = state.project.projects;
  const project = projects ? projects[id - 1] : null
  const color = state.color.color;
  return {
    id: id,
    project: project,
    color: color,
  }
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps))(ClockDetails)
