import React, { PureComponent } from 'react'
import { compose } from 'redux'
import { connect } from 'react-redux'
import ClockList from '../projects/ClockList'
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
});

class ClockDashboard extends PureComponent {

  render() {
    const { projects, color, cnumber, classes } = this.props;
    return (
      <div className={classes.root}>
        <Container fixed>
          <Grid container spacing={4}>
            <Grid item xs={12}>
            </Grid>
            <Grid item xs={12}>
            </Grid>
            <Grid item xs={6}>
              <ClockList projects={projects} color={color} cnumber={cnumber} />
            </Grid>
            <Grid item xs={6}>
            </Grid>
          </Grid>
        </Container>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    projects: state.project.projects,
    color: state.color.color,
    cnumber: state.color.pigment2,
  }
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps))(ClockDashboard)