import React from 'react';
import ImgResize from '../utils/ImgResize';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import LoopIcon from '@material-ui/icons/Loop';

export const ImgClockParts = ({ ele, posi, option, onClickfunc }) => {
  return (
    <IconButton
      size="small"
      onClick={onClickfunc}
      style={{ backgroundColor: 'transparent' }}
    >
      {option === 'second' ?
        <img
          src={`${process.env.PUBLIC_URL}/img/${Number(posi) + 1}/${ele}.png`}
          alt={ele}
        />
        :
        <img
          src={`${process.env.PUBLIC_URL}/img/${Number(posi) + 1}/${ele}.png`}
          alt={ele}
          width={ImgResize(Number(posi) + 1, ele).targetWidth}
          height={ImgResize(Number(posi) + 1, ele).targetHeight}
        />
      }
    </IconButton>
  );
}
export const ClockParts = ({ ele, func, size, onClickfunc, timedisplay }) => {
  return (
    <IconButton
      size="small"
      onClick={onClickfunc}
      style={{ backgroundColor: 'transparent' }}
    >
      <Typography component="h4" variant="h4">
        <Box fontWeight="fontWeightBold" m={0} color="#212111" fontSize={size}>
          {timedisplay ?
            <div>{func}</div>
            :
            <div>{ele}</div>
          }
        </Box>
      </Typography>
    </IconButton>
  )
}
export const ColonParts = () => (
  <IconButton size="small" style={{ backgroundColor: 'transparent' }}>
    <img
      src={`${process.env.PUBLIC_URL}/img/colon.png`}
      alt="colon"
      width="60%"
      height="60%"
    />
  </IconButton>
)
export const LoopParts = ({ onClickfunc }) => (
  <div className="loop">
    <IconButton
      size="small"
      onClick={onClickfunc}
      style={{ backgroundColor: 'transparent' }}
    >
      <LoopIcon color="secondary" fontSize="small" />
    </IconButton>
  </div>
)
