import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { compose } from 'redux'
import { parseTime } from '../../store/actions/clockActions'
import Zeroume from '../utils/Zeroume'
import ImgResize from '../utils/ImgResize'
import ClockBreadcrumbs from './ClockBreadcrumbs';
import { ImgClockParts, ClockParts, ColonParts, LoopParts } from './ClockParts'
import img1 from '../../img/black0039.png'
import img2 from '../../img/black0005.png'
import img3 from '../../img/black0075.png'
import img4 from '../../img/black0077.png'
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  card: {
    maxWidth: 900,
  },
  color: {
    one: '#01579b',
    two: '#0288d1',
    three: '#03a9f4',
    four: '#4fc3f7',
    five: '#81d4fa',
    six: '#b3e5fc',
    seven: '#e1f5fe',
  },
});

const imgs = [img1, img2, img3, img4,];
const dwNames = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];

class ClockStart extends PureComponent {
  state = {
    open: false,
    subopen: false,
    which: 0,
    date: parseTime(new Date()),
    imgid: 0,
    timedisplay: false,
    time: 0,
    changedisplay: false,
  }

  componentDidMount() {
    ImgResize(this.props.project.id, this.state.date.hour);
    ImgResize(this.props.project.id, this.state.date.minute);
    this.anime = setInterval(() => this.png(), 10000);
    this.timer = setInterval(() => this.tick(), 1000);
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.state.subopen !== prevState.open) {
      this.setState({
        date: parseTime(new Date()),
      });
      this.anime = setInterval(() => this.png(), 10000);
      this.timer = setInterval(() => this.tick(), 1000);
    }
    if (this.state.open !== prevState.subopen) {
      clearInterval(this.anime);
      clearInterval(this.timer);
    }
  }
  componentWillUnmount() {
    clearInterval(this.anime);
    clearInterval(this.timer);
  }
  tick = () => {
    this.setState({
      date: parseTime(new Date()),
      time: this.state.time + 1,
    });
    if (this.state.time === 3) {
      this.setState({
        timedisplay: true,
      })
    }
    if (this.state.date.second === 59) {
      ImgResize(this.props.project.id, this.state.date.minute + 1);
    }
    if (this.state.date.second === 58 && this.state.date.minute === 59) {
      ImgResize(this.props.project.id, this.state.date.hour + 1);
    }
  }
  png = () => {
    const imgid = this.state.imgid;
    if (imgid < 3) {
      this.setState({
        imgid: imgid + 1,
      });
    } else {
      this.setState({
        imgid: 0,
      });
    }
  }
  handleClickOpen = ele => {
    this.setState({
      open: true,
      which: ele,
    })
  }
  handleClose = () => {
    this.setState({
      open: false,
    })
  }
  handleClickChange = () => {
    this.setState({
      changedisplay: !this.state.changedisplay,
    })
  }
  render() {
    //console.log(this.props);
    const imgid = this.state.imgid;
    const { year, month, day, dw, hour, minute, second } = this.state.date;
    const dhour = Zeroume(hour);
    const dminute = Zeroume(minute);
    const dsecond = Zeroume(second);
    const { project, projectid, data, cnumber, classes } = this.props;
    if (project === undefined) return <Redirect to='/c' />
    if (project) {
      return (
        <div style={{ height: '525px', backgroundImage: `url(${imgs[imgid]})` }}>
          <div className={classes.root}>
            <Container fixed>
              <Grid container spacing={5}>
                <Grid item xs={12}>
                </Grid>
                <Grid>
                  <ClockBreadcrumbs breadcrumbsName='start' project={project} />
                </Grid>
                <Grid item xs={12}>
                </Grid>
                <Grid item xs={3}>
                </Grid>
                <Grid item xs={6}>
                  <Card
                    className={classes.card}
                    style={{
                      backgroundColor: `${cnumber}`,
                      color: 'white',
                    }}
                  >
                    <CardContent style={{ padding: '4px' }}>
                    </CardContent>
                    <CardContent style={{ padding: '8px' }}>
                    </CardContent>
                    {this.state.timedisplay ?
                      <CardContent style={{ padding: '3px' }}>
                        <Grid container>
                          <Grid item xs={1}>
                          </Grid>
                          <Grid item xs={10}>
                            {this.state.changedisplay ?
                              <div>
                                {project.img ?
                                  <div className="clock">
                                    <div className="leftimg">
                                      <ImgClockParts
                                        ele={hour}
                                        posi={projectid}
                                        option='hour'
                                        onClickfunc={() => this.handleClickOpen(hour)}
                                      />
                                    </div>
                                    <div className="colon">
                                      <ColonParts />
                                    </div>
                                    <div className="rightimg">
                                      <ImgClockParts
                                        ele={minute}
                                        posi={projectid}
                                        option='minute'
                                        onClickfunc={() => this.handleClickOpen(minute)}
                                      />
                                    </div>
                                  </div>
                                  :
                                  <div className="clock">
                                    <div className="leftdata">
                                      <ClockParts
                                        ele={hour}
                                        func={data[hour].name}
                                        size={19}
                                        onClickfunc={() => this.handleClickOpen(hour)}
                                        timedisplay={this.state.timedisplay}
                                      />
                                    </div>
                                    <div className="colon">
                                      <ColonParts />
                                    </div>
                                    <div className="rightdata">
                                      <ClockParts
                                        ele={minute}
                                        func={data[minute].name}
                                        size={19}
                                        onClickfunc={() => this.handleClickOpen(minute)}
                                        timedisplay={this.state.timedisplay}
                                      />
                                    </div>
                                  </div>
                                }
                              </div>
                              :
                              <div className="clock" style={{color: "block"}}>
                                {dhour}:{dminute}
                              </div>
                            }
                          </Grid>
                          <Grid item xs={1}>
                            <LoopParts onClickfunc={() => this.handleClickChange()} />
                          </Grid>
                        </Grid>
                      </CardContent>
                      :
                      <CardContent style={{ padding: '3px' }}>
                        <Grid container>
                          <Grid item xs={1}>
                          </Grid>
                          <Grid item xs={10}>
                            <div className="clock">
                              {year}&nbsp;{month}/{day}&nbsp;
                              {dwNames[dw]}&nbsp;{dhour}:{dminute}
                            </div>
                          </Grid>
                          <Grid item xs={1}>
                            <LoopParts onClickfunc={() => this.handleClickChange()} />
                          </Grid>
                        </Grid>
                      </CardContent>
                    }
                    <CardContent style={{ padding: '10px' }}>
                      <Typography component="div" color="inherit">
                        {this.state.timedisplay ?
                          <div>
                            {project.img ?
                              <div className="clock">
                                <div className="rightimg">
                                  <ImgClockParts
                                    ele={second}
                                    posi={projectid}
                                    option='second'
                                    onClickfunc={() => this.handleClickOpen(second)}
                                  />
                                </div>
                              </div>
                              :
                              <div className="clock">
                                <div className="rightdata">
                                  <ClockParts
                                    ele={second}
                                    func={data[second].name}
                                    size={29}
                                    onClickfunc={() => this.handleClickOpen(second)}
                                    timedisplay={this.state.timedisplay}
                                  />
                                </div>
                              </div>
                            }
                          </div>
                          :
                          <div className="clock">
                            <Typography component="h4" variant="h4">
                              <Box fontWeight="fontWeightBold" m={0} color="#212111" fontSize={29}>
                                {dsecond}
                              </Box>
                            </Typography>
                          </div>
                        }
                      </Typography>
                    </CardContent>
                    <CardContent style={{ padding: '12px' }}>
                    </CardContent>
                    <CardContent style={{ padding: '8px' }}>
                    </CardContent>
                    <CardContent style={{ padding: '1px' }}>
                      <div className="title2">
                        <Typography component="h6" variant="subtitle2">
                          <Box fontWeight="fontWeightBold" m={0} color="#212111" fontSize={7}>
                            {project.title}
                          </Box>
                        </Typography>
                      </div>
                    </CardContent>
                    <Dialog
                      open={this.state.open}
                      onClose={this.handleClose}
                      aria-labelledby="dialog-title"
                      aria-describedby="dialog-description"
                      style={{ backgroundColor: '#01579b' }}
                    >
                      <DialogTitle id="dialog-title">
                        {project.img ?
                          <div>
                            <div className="dialogdescription1">
                              <img
                                src={`${process.env.PUBLIC_URL}/img/${project.id}/${this.state.which}.png`}
                                alt={this.state.which}
                              />
                            </div>
                            <div className="dialogdescription2">
                              <Typography component="h4" variant="h4">
                                <Box fontWeight="fontWeightBold" m={0} color="black">
                                  &nbsp; = {this.state.which}
                                </Box>
                              </Typography>
                            </div>
                          </div>
                          :
                          <div>
                            <div className="dialogdescription1">
                              <Typography component="h4" variant="h4">
                                <Box fontWeight="fontWeightBold" m={0} color="black">
                                  {data[this.state.which].name}
                                </Box>
                              </Typography>
                            </div>
                            <div className="dialogdescription1">
                              <Typography component="h4" variant="h4">
                                <Box fontWeight="fontWeightBold" m={0} color="black">
                                  &nbsp; = {this.state.which}
                                </Box>
                              </Typography>
                            </div>
                          </div>
                        }
                      </DialogTitle>
                      <DialogContent>
                        <DialogContentText id="dialog-description">
                          {project.img ?
                            <Typography component={'span'} variant={'body2'}>
                              {project.content}
                            </Typography>
                            :
                            <Typography component={'span'} variant={'body2'}>
                              {data[this.state.which].description}
                            </Typography>
                          }
                        </DialogContentText>
                      </DialogContent>
                      <DialogActions>
                        <Button
                          onClick={this.handleClose}
                          variant="contained"
                          color="secondary"
                        >
                          OK
                        </Button>
                      </DialogActions>
                    </Dialog>
                  </Card>
                </Grid>
                <Grid item xs={3}>
                </Grid>
              </Grid>
            </Container>
          </div>
        </div>
      )
    }
  }
}

const mapStateToProps = (state, ownProps) => {
  //console.log(ownProps);
  const id = ownProps.match.params.id;
  const projects = state.project.projects;
  const data = state.project[`data${id}`];
  const project = projects ? projects[id - 1] : null
  const cnumber = state.color.pigment1;
  return {
    project: project,
    projectid: id - 1,
    data: data,
    cnumber: cnumber,
  }
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps))(ClockStart)