import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { compose } from 'redux'
import { parseTime } from '../../store/actions/clockActions'
import Zeroume from '../utils/Zeroume'
import ImgResize from '../utils/ImgResize'
import ClockBreadcrumbs from './ClockBreadcrumbs'
import { ImgClockParts, ClockParts, ColonParts, LoopParts } from './ClockParts'
import img1 from '../../img/black0039.png'
import img2 from '../../img/black0005.png'
import img3 from '../../img/black0075.png'
import img4 from '../../img/black0077.png'
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  card: {
    maxWidth: 900,
  },
});

class ClockShuffleStart extends PureComponent {
  state = {
    open: false,
    subopen: false,
    which: 0,
    date: parseTime(new Date()),
    imgs: [img1, img2, img3, img4,],
    imgid: 0,
    selectedid: this.props.location.state.randomarray[0],
    dwNames: ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'],
    timedisplay: false,
    time: 0,
    changedisplay: false,
  }
  componentDidMount() {
    window.scrollTo(0, 0);
    ImgResize(Number(this.state.selectedid) + 1, this.state.date.hour);
    ImgResize(Number(this.state.selectedid) + 1, this.state.date.minute);
    this.anime = setInterval(() => this.png(), 10000);
    this.timer = setInterval(() => this.tick(), 1000);
    this.rantimer = setInterval(() => this.randomid(), 3000);
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.state.subopen !== prevState.open) {
      this.setState({
        date: parseTime(new Date()),
      });
      this.anime = setInterval(() => this.png(), 10000);
      this.timer = setInterval(() => this.tick(), 1000);
      this.rantimer = setInterval(() => this.randomid(), 3000);
    }
    if (this.state.open !== prevState.subopen || this.state.selectedid === undefined) {
      clearInterval(this.anime);
      clearInterval(this.timer);
      clearInterval(this.rantimer);
    }
  }
  componentWillUnmount() {
    clearInterval(this.anime);
    clearInterval(this.timer);
    clearInterval(this.rantimer);
  }
  tick = () => {
    this.setState({
      date: parseTime(new Date()),
      time: this.state.time + 1,
    });
    if (this.state.time === 3) {
      this.setState({
        timedisplay: true,
      })
    }
    if (this.state.date.second === 59) {
      ImgResize(Number(this.state.selectedid) + 1, this.state.date.minute + 1);
    }
    if (this.props.location.state.randomarray[1] !== undefined) {
      if ((this.state.time % 3) === 2) {
        ImgResize(Number(this.props.location.state.randomarray[1]) + 1, this.state.date.hour);
        ImgResize(Number(this.props.location.state.randomarray[1]) + 1, this.state.date.minute);
      }
    }
  }
  png = () => {
    const imgid = this.state.imgid;
    if (imgid < 3) {
      this.setState({
        imgid: imgid + 1
      });
    }
    if (imgid === 3) {
      this.setState({
        imgid: 0,
      });
    }
  }
  randomid = () => {
    if (this.state.time >= 3) {
      this.setState({
        selectedid: this.props.location.state.randomarray.splice(1, 1)[0],
        date: parseTime(new Date()),
      });
    }
  }
  handleClickOpen = ele => {
    this.setState({
      open: true,
      which: ele,
    })
  }
  handleClose = () => {
    this.setState({
      open: false,
    })
  }
  handleClickChange = () => {
    this.setState({
      changedisplay: !this.state.changedisplay,
    })
  }
  render() {
    console.log(this.state);
    const selectedid = this.state.selectedid;
    const imgs = this.state.imgs;
    const imgid = this.state.imgid;
    const { year, month, day, dw, hour, minute, second } = this.state.date;
    const dhour = Zeroume(hour);
    const dminute = Zeroume(minute);
    const dsecond = Zeroume(second);
    const { projects, dataintegration, color, classes } = this.props;
    if (projects[selectedid] === undefined) return <Redirect to='/c' />
    if (projects[selectedid]) {
      return (
        <section style={{ height: '525px', backgroundImage: `url(${imgs[imgid]})` }}>
          <div className={classes.root}>
            <Container fixed>
              <Grid container spacing={5}>
                <Grid item xs={12}>
                </Grid>
                <Grid>
                  <ClockBreadcrumbs breadcrumbsName='shuffle' shuffle={projects[selectedid].title} />
                </Grid>
                <Grid item xs={12}>
                </Grid>
                <Grid item xs={3}>
                </Grid>
                <Grid item xs={6}>
                  <Card
                    className={classes.card}
                    style={{
                      backgroundImage: `url(${process.env.PUBLIC_URL}/img/card/light${color}.png)`,
                      color: 'white'
                    }}
                  >
                    <CardContent style={{ padding: '4px' }}>
                    </CardContent>
                    <CardContent style={{ padding: '8px' }}>
                    </CardContent>
                    {this.state.timedisplay ?
                      <CardContent style={{ padding: '3px' }}>
                        <Grid container>
                          <Grid item xs={1}>
                          </Grid>
                          <Grid item xs={10}>
                            {this.state.changedisplay ?
                              <div>
                                {projects[selectedid].img ?
                                  <div className="clock">
                                    <div className="leftimg">
                                      <ImgClockParts
                                        ele={hour}
                                        posi={selectedid}
                                        option='hour'
                                        onClickfunc={() => this.handleClickOpen(hour)}
                                      />
                                    </div>
                                    <div className="colon">
                                      <ColonParts />
                                    </div>
                                    <div className="rightimg">
                                      <ImgClockParts
                                        ele={minute}
                                        posi={selectedid}
                                        option='minute'
                                        onClickfunc={() => this.handleClickOpen(minute)}
                                      />
                                    </div>
                                  </div>
                                  :
                                  <div className="clock">
                                    {dataintegration.map((ele, idx) => {
                                      return (
                                        <div key={idx}>
                                          {ele.id === Number(projects[selectedid].id) &&
                                            <div>
                                              <div className="leftdata">
                                                <ClockParts
                                                  ele={hour}
                                                  func={ele.data[hour].name}
                                                  size={19}
                                                  onClickfunc={() => this.handleClickOpen(hour)}
                                                  timedisplay={this.state.timedisplay}
                                                />
                                              </div>
                                              <div className="colon">
                                                <ColonParts />
                                              </div>
                                              <div className="centerdata">
                                                <ClockParts
                                                  ele={minute}
                                                  func={ele.data[minute].name}
                                                  size={19}
                                                  onClickfunc={() => this.handleClickOpen(minute)}
                                                  timedisplay={this.state.timedisplay}
                                                />
                                              </div>
                                            </div>
                                          }
                                        </div>
                                      )
                                    })}
                                  </div>
                                }
                              </div>
                              :
                              <div className="clock">
                                {dhour}:{dminute}
                              </div>
                            }
                          </Grid>
                          <Grid item xs={1}>
                            <LoopParts onClickfunc={() => this.handleClickChange()} />
                          </Grid>
                        </Grid>
                      </CardContent>
                      :
                      <CardContent style={{ padding: '3px' }}>
                        <Grid container>
                          <Grid item xs={1}>
                          </Grid>
                          <Grid item xs={10}>
                            <div className="clock">
                              {year}&nbsp;{month}/{day}&nbsp;
                              {this.state.dwNames[dw]}&nbsp;{dhour}:{dminute}
                            </div>
                          </Grid>
                          <Grid item xs={1}>
                            <LoopParts onClickfunc={() => this.handleClickChange()} />
                          </Grid>
                        </Grid>
                      </CardContent>
                    }
                    <CardContent style={{ padding: '16px' }}>
                      <Typography component="div" color="inherit">
                        {this.state.timedisplay ?
                          <div>
                            {projects[selectedid].img ?
                              <div className="clock">
                                <div className="rightimg">
                                  <ImgClockParts
                                    ele={second}
                                    posi={selectedid}
                                    option='second'
                                    onClickfunc={() => this.handleClickOpen(second)}
                                  />
                                </div>
                              </div>
                              :
                              <div className="clock">
                                {dataintegration.map((ele, idx) => {
                                  return (
                                    <div key={idx}>
                                      {ele.id === Number(projects[selectedid].id) &&
                                        <div>
                                          <div className="rightdata">
                                            <ClockParts
                                              ele={second}
                                              func={ele.data[second].name}
                                              size={29}
                                              onClickfunc={() => this.handleClickOpen(second)}
                                              timedisplay={this.state.timedisplay}
                                            />
                                          </div>
                                        </div>
                                      }
                                    </div>
                                  )
                                })}
                              </div>
                            }
                          </div>
                          :
                          <div className="clock">
                            <Typography component="h4" variant="h4">
                              <Box fontWeight="fontWeightBold" m={0} color="#212111" fontSize={29}>
                                {dsecond}
                              </Box>
                            </Typography>
                          </div>
                        }
                      </Typography>
                    </CardContent>
                    <CardContent style={{ padding: '12px' }}>
                    </CardContent>
                    <CardContent style={{ padding: '8px' }}>
                    </CardContent>
                    <CardContent style={{ padding: '4px' }}>
                    </CardContent>
                    <Dialog
                      open={this.state.open}
                      onClose={this.handleClose}
                      aria-labelledby="dialog-title"
                      aria-describedby="dialog-description"
                      style={{ backgroundColor: '#01579b' }}
                    >
                      <DialogTitle id="dialog-title">
                        {projects[selectedid].img ?
                          <div>
                            <div className="dialogdescription1">
                              <img
                                src={`${process.env.PUBLIC_URL}/img/${projects[selectedid].id}/${this.state.which}.png`}
                                alt={this.state.which}
                              />
                            </div>
                            <div className="dialogdescription2">
                              <Typography component="h4" variant="h4">
                                <Box fontWeight="fontWeightBold" m={0} color="black">
                                  &nbsp; = {this.state.which}
                                </Box>
                              </Typography>
                            </div>
                          </div>
                          :
                          <div>
                            {dataintegration.map((ele, idx) => {
                              return (
                                <div className="dialogdescription1" key={idx}>
                                  {ele.id === Number(projects[selectedid].id) &&
                                    <Typography component="h4" variant="h4">
                                      <Box fontWeight="fontWeightBold" m={0} color="black">
                                        <div>
                                          {ele.data[this.state.which].name}
                                        </div>
                                      </Box>
                                    </Typography>
                                  }
                                </div>
                              )
                            })}
                            <div className="dialogdescription1">
                              <Typography component="h4" variant="h4">
                                <Box fontWeight="fontWeightBold" m={0} color="black">
                                  &nbsp; = {this.state.which}
                                </Box>
                              </Typography>
                            </div>
                          </div>
                        }
                      </DialogTitle>
                      <DialogContent>
                        <DialogContentText id="dialog-description">
                          {projects[selectedid].img ?
                            <Typography component={'span'} variant={'body2'}>
                            {projects[selectedid].content}
                            </Typography>
                            :
                            <Typography component={'span'} variant={'body2'}>
                              {dataintegration.map((ele, idx) => {
                                return (
                                  <div key={idx}>
                                    {ele.id === Number(projects[selectedid].id) &&
                                      <div>{ele.data[this.state.which].description}</div>
                                    }
                                  </div>
                                )
                              })}
                            </Typography>
                          }
                        </DialogContentText>
                      </DialogContent>
                      <DialogActions>
                        <Button
                          onClick={this.handleClose}
                          variant="contained"
                          color="secondary"
                        >
                          OK
                        </Button>
                      </DialogActions>
                    </Dialog>
                  </Card>
                </Grid>
                <Grid item xs={3}>
                </Grid>
              </Grid>
            </Container>
          </div>
        </section>
      )
    }
  }
}

const mapStateToProps = (state) => {
  console.log(state)
  const projects = state.project.projects;
  const rawarray = Object.keys(projects)
    .filter(project => !projects[project].img).filter(project => projects[project].level !== '');
  const dataarray =
    rawarray.map(number => {
      return Number(number) + 1
    })
  const dataintegration =
    dataarray.map(number => {
      return (
        { id: number, data: state.project[`data${number}`] }
      )
    });
  const color = state.color.color;
  return {
    projects: projects,
    dataintegration: dataintegration,
    color: color,
  }
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps))(ClockShuffleStart)
